.Footer {
    text-align: center;
    position: relative;
    margin-top:30px ;
    background-color: rgb(45,39,39);
    padding: 30px;
    color: white;
    a {
        color: inherit;
        text-decoration: none;
    }
    ::before{
        content: "";
        background-color: #d35e0f;
        position: absolute;
        top: -1px;
        left: 0;
        width: 100%;
        height: 2%;
        box-shadow: 0px -2px 2px #d35e0f;
    }

}